@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.imageContainer {
    margin-left: 5%;
    margin-right: 5%;
    margin-top:3%;
    position:relative;
}
.card {
    border-style: solid;
    border-radius: 5%;
    height: 100%;
    width: auto;
    background-color:white;
    border-color: white;
    padding-bottom:5%;
    padding-top: 2%;
    position:relative;
}

.card:hover {
     background-color: rgba(235, 232, 234, 0.68);
     border-color:  rgba(235, 232, 234, 0.68);
  }

.imgCard{  
    border-radius: 5%;
    display: block;
    margin-left: auto;
    margin-right: auto;
}


.more {
    color: rgba(133, 126, 131, 0.673);
    font-size: 15px;
    float: right;
    border-radius: 100%;
    margin-top:2px;
    margin-right: 5px;
    border: none;
    background: white;
}

.more:hover {
    background-color: rgba(210, 205, 208, 0.68);
}

.save{
 background-color: red;
 padding:6%;
 color: white;
 border-radius: 10%;
 border-top-left-radius: 0%;
 border-bottom-left-radius: 0%; 
}

.tag{
 background-color: white;
 color:black;
 border-radius: 10%;
 border-top-right-radius: 0%;
 border-bottom-right-radius: 0%;
 padding:6%;
 width: 120px;
 margin-left:6%;
 
}

.narrow{
    float: right;
    margin-top:5px;
    color:rgba(0, 0, 0, 0.666);
}
.modal{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: #F5F5F5;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top:5%;
}

 .close-btn{
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  background-color: #F5F5F5;
  padding: 0.5rem 1rem;
  border-radius: 30%;
} 

.close-btn:hover{
  background-color:rgb(240, 234, 239);
}

.cross{
  color: rgba(133, 126, 131, 0.673);
  font-size: 25px;
}

.header-modal{
  margin-top:3%;
  padding-top:5px;
  display:flex;
  height: 60px !important;
  width:70%;
  background-color:white;
  display: inline-flex;
  justify-content: space-between;
}

 .header-two{
  display:inline-flex;
  width:40%;
}  

.dots-btn{
  padding: 10px;
  color: rgba(133, 126, 131, 0.673);
  border-radius: 30%;
  border-style: none;
}
.dots-btn:hover{
  background-color:#F5F5F5;
}

.upload{
  margin-right:10px;
}
.fa-ellipsis-h{
  color: rgba(133, 126, 131, 0.673);
  font-size: 20px;
  margin-left: 10px;
}

.btn-send{
  padding: 10px 14px;
  height: 90%;
  box-sizing: border-box;
  cursor: pointer;
  color: #333;
  font-size: 16px;
  text-align: center;
  font-weight: 700;
  line-height: normal;
  border-radius: 8px;
  background-color: #efefef;
  margin-right: 5%;
}
.btn-send:hover, .btn-tag:hover{
  background-color:rgb(240, 234, 239);
}

.narrow-up{
 float: right;
} 

.btn-tag{
  padding: 10px 14px;
  height: 90%;
  width:40%;
  box-sizing: border-box;
  cursor: pointer;
  color: #333;
  font-size: 16px;
  text-align: left;
  font-weight: 700;
  line-height: normal;
  border-radius: 8px;
  background-color: #efefef;
  border-top-right-radius: 0%;
  border-bottom-right-radius: 0%;
}

.btn-save{
  height: 90%;
  padding: 10px 14px;
  box-sizing: border-box;
  cursor: pointer;
  color: #fff;
  font-size: 16px;
  text-align: center;
  font-weight: 700;
  line-height: normal;
  background-color: #e60023;
  border-radius: 8px;
  border-top-left-radius: 0%;
  border-bottom-left-radius: 0%;
}
.btn-save:hover{
  background-color: #b71c1c;
}


.containerImgyText{
  background-color:white;
  width: 70%;
  flex-direction: row;
  display: flex;
  height: 760px;
  padding-top:20px; 
} 

.img-container{
  padding-left:1%;
  box-sizing: border-box;
  display: block;
  flex-grow: 8;  
}

.img-modal{
  border-radius: 8px;
  margin: 0px auto;
  width: auto;
  height: auto;
}

.text-container{
  background-color: #fff;
  box-sizing: border-box;
  display: block;
  flex-grow: 1;
}

.container-modal{
  position: fixed;
  top: 55px;
  display: flex;
  width: 100%;
  background: rgb(238, 238, 238);
}


.link{
  /* padding: 4px 80px;  */
  padding:2%;
  background-color: #efefef;
  border-radius: 8px;
  cursor: pointer;
  height: 40px;
  color: #333;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: -.4px;
  width:336px; 
  margin-left: 48px;
  margin-top: 15px;
  display: flex;
}
.link:hover{
  background-color: #bdbdbd;
}
.line{
  border: 1px solid #efefef;
  width: 330px;
  margin-right: 24px;

}
.text{
  text-align: left;
  color: #333;
  font-size: 21px;
  font-weight: 700;
  letter-spacing: -.4px;
  margin-left:80px;
  margin-bottom: 5px;
}

.btn-follow{
  background-color: #e60023;
  border-radius: 8px;
  padding: 10px 14px;
  color: #fff;
  font-size: 16px;
  text-align: center;
  font-weight: 700;
  letter-spacing: -.4px;
  border: none;
  cursor: pointer;
  margin-left: 290px;
  margin-top:-19px;
}
.btn-follow:hover{
  background-color: #b71c1c;
}

.btn-numberphotos{
  background-color: #efefef;
  color: #333;
  padding: 8px 16px;
  cursor: pointer;
  border-radius: 20px;
  margin-left: 80px;
  margin-top: -9px;
  font-size: 16px;
  font-weight: 700;
}
.btn-numberphotos:hover{
  text-decoration: underline;
}
.comments{
  display: inline-block;
  margin-left: 15px;
  color: #8e8e8e;
  font-weight: bold;
}
.comments:hover{
  text-decoration: underline;
  color: #333;
  font-size: 16px;
}
.navbar{
text-align: center;
display: inline-flex; 
font-family: 'Roboto', sans-serif;
}

.bar{
    display: flex;
    padding-top: 10px;
}

.pinterest{
    color:#B10F2E;
    font-size: 30px;
    display: flex;
}

.loop {
    color: rgba(133, 126, 131, 0.673);
    font-size: 30px;
    background-color:#F5F5F5;
    padding: 6px;
    border-top-left-radius: 10%;
    border-bottom-left-radius: 10%;
}

.search {
   border-style: solid;
   background-color:#F5F5F5;
   border-color: #F5F5F5;
   border-right-color:#E6E6EA;
   width: 600px;
   height: 30px;
}
 .custom-select {
   border: 0px;
   width: 90px;
   height: 30px;
   background-color:#F5F5F5;
   font-family: 'Roboto', sans-serif;
   font-size: 12px;
   -webkit-appearance: button;
   border-top-left-radius: 0%;
   border-bottom-left-radius: 0%;
   overflow: hidden;
   margin-right:20px;
   }
 
.icons {
    color: rgba(133, 126, 131, 0.673);
    font-size: 25px;
}

.navbar button{
    padding: 12px;
    color: rgba(133, 126, 131, 0.673);
    border-radius: 30%;
    border-style: none;
}

.navbar button:hover {
    background-color:#F5F5F5;
  }

.buttonbar button {
    padding: 14px;
    border-radius: 10%;
    border-style: solid;
    color:white;
    margin-right: 4px;
 
}

.buttonbar {
    margin-left: 5%;
    margin-right: 5%;
    margin-top: 1%;
}

